import {Map, View} from "ol";
import TileLayer from "ol/layer/Tile";
import OSM from "ol/source/OSM";
import TileWMS from "ol/source/TileWMS";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import GeoJSON from 'ol/format/GeoJSON.js';

import {
    Attribution,
    ScaleLine,
    OverviewMap,
    ZoomToExtent,
    defaults as defaultControls
} from "ol/control";

let basemapLayer = new TileLayer({
    source: new OSM({
      url: "https://cartocdn_{s}.global.ssl.fastly.net/base-midnight/{z}/{x}/{y}.png",
    }),
});
  
let overviewLayer = new TileLayer({
    source: new OSM({
      url: "https://cartocdn_{s}.global.ssl.fastly.net/base-midnight/{z}/{x}/{y}.png",
    }),
});


let overviewMapControl = new OverviewMap({
    className: "ol-overviewmap ol-custom-overviewmap",
    layers: [overviewLayer],
    collapsed: false,
});

let zoomToExtentControl = new ZoomToExtent({
    // ESPG:3857
    extent: [-15000000, -24000000, 15000000, 29000000],
});

function getScaleControl() {
    let control = new ScaleLine({
      units: "metric",
      bar: false,
      steps: 4,
      text: true,
      minWidth: 140,
    });
    return control;
}

let BrasilFrequencyLayer = new TileLayer({
    source: new TileWMS({
      url: "https://sedac.ciesin.columbia.edu/geoserver/wms",
      params: {
        LAYERS: "ndh:ndh-earthquake-frequency-distribution",
        TILES: true,
      },
    }),
});

let BrasilVectorLayer = new VectorLayer({
    source: new VectorSource ({
        format: new GeoJSON(),
        url:
        // "https://earthquake.usgs.gov/earthquakes/feed/v1.0/summary/all_day.geojson",
        ".\htdocs\GitHub\desafio-estag-petrec-2020\estados.geojson"
        

    })
});

const map = new Map({
    target: 'map',
    layers: [ basemapLayer,BrasilFrequencyLayer,BrasilVectorLayer
    ],
    view: new View({
        center: [0,0],
        zoom: 0
    }),
    controls: defaultControls({
        attributionOptions: { collapsible: true },
      }).extend([overviewMapControl,zoomToExtentControl, getScaleControl()]),
});